.radio {
    display: flex;
    align-items: center;
    gap: 10px;
    cursor: pointer;
}

.radio input {
    display: none;
}

.radio::before {
    content: "";
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 1px solid #ccc;
    border-radius: 50%;
    margin-right: 8px;
}

.radio span {
    font-size: 16px;
}

.radio:checked::before {
    content: "\2713";
    font-size: 16px;
    color: #fff;
    background-color: #007bff;
    border-color: #007bff;
}

.label {
    display: flex;
    align-items: center;
    gap: 5px;
    cursor: pointer;
    font-size: 16px;
}

.options {
    display: flex;
    flex-direction: column;
}
